* {
  box-sizing: border-box;
}

body {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
  width: 100%;
  min-height: 100%;
  text-align: center;
  background-color: #333333;
  color: white;
}

.App {
  height: 100vh;
  width: 100%;
  font-family: sans-serif;
  margin: 0px;
  padding: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
}
::-webkit-scrollbar {
  width: 0px;
  background: transparent; /* make scrollbar transparent */
}
.calculator-container {
  width: 300px;
  height: 450px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  border-radius: 10px;
  box-shadow: 10px 10px 20px 5px rgba(0, 0, 0, 0.836),
    -4px -4px 20px 5px rgba(255, 255, 255, 0.3);
}

.display {
  width: 100%;
  height: 100px;
  box-shadow: -4px -4px 5px 0px rgba(255, 255, 255, 0.3) inset,
    4px 4px 5px 0px rgba(0, 0, 0, 0.836) inset;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}

.operation-div {
  width: 100%;
  height: 60%;
  padding-bottom: 5px;
  font-weight: bold;
  text-align: right;
  overflow-x: scroll;
  overflow-y: hidden;
  word-wrap: normal;
  font-size: 1.5rem;
}

.result-div {
  height: 70%;
  font-weight: bold;
  text-align: right;
  padding-bottom: 5px;
  font-size: 2rem;
  overflow-x: scroll;
  overflow-y: hidden;
  word-wrap: normal;
  color: rgb(51, 245, 132);
}

.row {
  height: 60px;
  margin-bottom: 5px;

  display: flex;
}

.span {
  cursor: pointer;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  font-weight: bold;
  width: 25%;
  margin: 2px;
  border-radius: 5px;
  box-shadow: -2px -2px 2px 0px rgba(255, 255, 255, 0.3),
    2px 2px 2px 0px rgba(0, 0, 0, 0.836);
}

.double-span {
  cursor: pointer;
  width: 51%;
  margin: 2px;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  height: 100%;
  border-radius: 5px;
  box-shadow: -2px -2px 2px 0px rgba(255, 255, 255, 0.3),
    2px 2px 2px 0px rgba(0, 0, 0, 0.836);
}

.span:active,
.double-span:active {
  box-shadow: -2px -2px 2px 0px rgba(255, 255, 255, 0.3) inset,
    2px 2px 2px 0px rgba(0, 0, 0, 0.836) inset;
}

.operation {
  color: rgb(241, 146, 56);
}

.secondary {
  color: rgb(233, 215, 50);
}

.equal {
  color: rgb(51, 245, 132);
}

